<template>
  <div class="course-class-practise">
    <head-top title="随堂测验">
    </head-top>

    <div class="sub_module" v-if="dataList && dataList.length>0">
      <div v-for="(item, index) in dataList" :key="index" class="list-item van-hairline--bottom"
           @click="handleClick(item, index)">
        <div class="flex-between">
          <span>{{item.name}}</span>
          <van-tag plain v-if="item.state === 1">未测验</van-tag>
          <van-tag plain type="primary" v-else-if="item.state === 2">进行中</van-tag>
          <van-tag plain type="danger" v-else>已结束</van-tag>
        </div>
        <div class="flex-between item-remark">
          <span>题数:{{item.number}}</span>
          <span>总分:{{item.score}}</span>
          <span v-if="item.state !== 1">时长:{{item.time === 0 ? '不限' : item.time}}</span>
          <span>
            <span v-if="item.startTime">开始:{{item.startTime}}</span>
          </span>
        </div>
      </div>
    </div>
    <r-lack-data v-else></r-lack-data>

    <!-- 新建分享-->
    <van-popup v-model="operateVisible" position="bottom">
      <van-cell title="题库预览" @click="toPreview"></van-cell>
      <van-cell title="开始测验" v-if="!selectItem || selectItem.state=== 1" style="text-align: left">
        <template #title>
          <div class="flex-between">
            <div class="flex">
              <span>计时：</span>
              <van-stepper integer min="0" max="1000" v-model="testTime"/>
              <span style="padding-left: 10px;">分(0表示不计时)</span>
            </div>
            <van-button type="primary" size="small" @click="toStart">开始测验</van-button>
          </div>
        </template>
      </van-cell>
      <!--      <van-cell title="结束答题" v-if="selectItem && selectItem.state===2" @click="toStop"></van-cell>-->
      <van-cell title="提交人员" v-if="selectItem && (selectItem.state=== 2 || selectItem.state=== 3)"
                @click="toViewSubmit"></van-cell>
      <van-cell title="查看作答" v-if="selectItem && selectItem.state=== 3"
                @click="toViewAnswer"></van-cell>
      <van-cell title="得分统计" v-if="selectItem && selectItem.state===3" @click="toViewScore"></van-cell>
    </van-popup>
  </div>
</template>

<script>
import { formatDate } from "@/utils/formatData";

export default {
  name: "classPractise",
  data () {
    return {
      dataList: [],
      operateVisible: false,
      selectIndex: -1,
      selectItem: null,
      testTime: 0
    };
  },
  created () {
    this.getPractiseList();
  },
  methods: {
    getPractiseList () {
      this.dataList = [];
      this.$mainHttp.classPractise.CoursePractise({
        chID: this.$store.getters.classHourID,
        ciID: this.$store.getters.courseID,
        classID: this.$store.getters.classID
      }).then(res => {
        if (res.Result) {
          res.Datas.sort((a, b) => {
            return a["StartTime"] > b["StartTime"] ? -1 : 1;
          });

          res.Datas.forEach(item => {
            this.dataList.push({
              // CIPTime: 0
              // State: 0
              taskID: item["CIPID"] + "",
              testID: item["CPID"] + "",
              id: item["PLID"] + "",
              name: item["PLTitle"] + "",
              number: item["SubjectTotal"] || 0,
              score: item["SubjectScore"] || 0,
              state: item["CIPState"],
              time: item["CIPTime"],
              createTime: formatDate(item["CreateTime"], "yyyy-MM-dd"),
              startTime: item["StartTime"] ? item["StartTime"].substring(0, 16) : ""
            });
          })
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    handleClick (item, index) {
      this.selectIndex = index;
      this.selectItem = item;
      this.testTime = 0;
      this.operateVisible = true;
    },
    toPreview () {
      // this.operateVisible = false;
      // this.$router.push("/course/practiseView");
      this.$store.commit("SET_PRACTISE_LIBRARY", this.selectItem);
      this.$router.push("/practise-library/view");
    },
    toStart () {
      this.$gatewayHttp.classPractise.setStart({
        cpID: this.selectItem.testID,
        ciID: this.$store.getters.courseID,
        time: this.testTime,
        classID: this.$store.getters.classID,
        plID: this.selectItem.id,
        subjectTotal: this.selectItem.number
      }).then(res => {
        if (res.Result) {
          this.getPractiseList();
          this.operateVisible = false;
          this.$toast("下发成功");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("下发失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    },
    toStop () {
      this.dataList[this.selectIndex].state = 1;
      this.operateVisible = false;
    },
    toViewSubmit () {
      this.operateVisible = false;
      this.$store.commit("SET_PRACTISE_LIBRARY", this.selectItem);
      this.$router.push("/course/class-practise/response");
    },
    toViewAnswer () {
      this.operateVisible = false;
      this.$store.commit("SET_PRACTISE_LIBRARY", this.selectItem);
      this.$router.push("/course/class-practise/detail");
    },
    toViewScore () {
      this.operateVisible = false;
      this.$store.commit("SET_PRACTISE_LIBRARY", this.selectItem);
      this.$router.push("/course/class-practise/score");
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../../styles/common/base";
.course-class-practise {
  min-height: 100%;
  background-color: #fff;

  .list-item {
    padding: 12px 0 6px 0;
    font-size: @font-info;
    line-height: 27px;

    .item-remark {
      color: #888;
    }
  }
}
</style>
